input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: none;
  transition: background-color 5000s ease-in-out 0s;
}

.contacts {
  &__wrapper {
    @include pageGrid;
  }

  &__form {
    display: block;
    padding: 0;
    width: 100%;

    &--row {
      width: 100%;
      margin-bottom: 16px;

      grid-column: 1 / -1;

      @include onTablet {
        grid-column: 1 / 4;
      }

      @include onDesktop {
        grid-column: 1 / 6;
      }
    }

    &--input {
      font-family: "Poppins", sans-serif;

      box-sizing: border-box;
      width: 100%;
      line-height: 20px;
      padding: 14px 16px;
      border-radius: 40px;
      border: none;
    }

    &--message {
      font-family: "Poppins", sans-serif;

      box-sizing: border-box;
      width: 100%;
      height: 148px;

      padding: 14px 16px;
      line-height: 20px;

      border-radius: 16px;

      margin-bottom: 16px;
      border: none;
      resize: none;
    }

    &--button {
      padding: 14.5px 123px;
      margin-bottom: 48px;
      width: 100%;

      border: none;
      border-radius: 40px;

      font-weight: 700;
      font-size: 16px;
      line-height: 27px;
      text-align: center;

      color: $main-bgc;

      cursor: pointer;

      @include hover(box-shadow, 1px 4px 16px rgba(232, 232, 240, 0.4));

      &:active {
        box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
      }
    }

    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__block {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / 6;
    }

    @include onDesktop {
      grid-column: 8 / 11;
    }

    &-text {
      margin-bottom: 24px;

      @include hover(transform, scale(1.05));

      &--title {
        font-weight: 400;
        font-size: 14px;
        line-height: 19.6px;

        margin-bottom: 8px;
      }

      &--description {
        font-weight: 500;
        font-size: 16px;
        line-height: 22.4px;
      }
    }
  }
}

.link {
  color: #fff;
  text-decoration: none;
  font-style: normal;
  font-weight: 500;
}
